  <template>
    <div class="hello">
        <div class="top">
          <div class="top_left">北京学为教育集团OA系统</div>
          <div class="top_right">
            <span class="ps">可用额度</span>
            <span class="ps">&nbsp;:&nbsp;</span>
            <span class="ps">20000.00元</span>
            <span class="sx">&nbsp;</span>
            <span class="ps">销售经理</span>
            <div class="top_yh_div">
              <span class="top_yh">{{ userName }}</span>
              <span class="top_yh_xj"></span>
              <ul class="top_yh_ul">
                <li>修改密码</li>
                <li v-on:click="loginout">退出</li>
              </ul>
            </div>
          </div>
        </div>
        <div class="x_body">
          <div class="x_left" id="x_left">
            <div class="leftList" v-for="(list,k) in wyLeftList">
                 <div class="firstLevel"  v-bind:class="[wyLeftList[k]['menuj'],list.permissionIconUrl]" v-on:click="parentClick(k)">{{ list.name }}<span v-if="list.child.length>0">&nbsp;</span></div>
                
                 <ul v-if="list.sonShow">
                    <li v-for="(secondList,v) in list.child" v-bind:class="[secondList.permissionIconUrl]" class="secondLevel menuj" v-on:click="childrenClick(k, v)"><a v-bind:class="{'point': secondList.point}" style="display: inline-block;width: 100%" :href="secondList.childData.permissionUrl">{{ secondList.childData.permissionName }}</a><span v-if="secondList.children">&nbsp;</span></li>                    
                </ul>
            </div>
          </div>
          <div class="x_right">
            <router-view/>
          </div>
        </div>
    </div>
  </template>

  <script>
  export default {
    name: 'MainIndex',
    data () {
      return {
        userName: localStorage.getItem("userName"),
        path: this.OACommon["publicDef"]["qpath"],
        leftList: "",
        check: "",
        wyLeftList: [
          {
            "sonShow": false,
            "menuj" : "menuj",
            "name": '',
            "child": []
          }
        ],
        data: []

      }
    },
    created: function(){
      if(!this.OACommon.getCookie("userId")){
        this.$router.push('/'); 
      }
      this.getTreeData();

    },
    methods: {
      parentClick: function(k){
        this.wyLeftList[k]["sonShow"] = !this.wyLeftList[k]["sonShow"];
       

      },
      childrenClick: function(k, ki){
          var wyList = this.wyLeftList;
          for(var i=0;i<wyList.length;i++){
            if(wyList[i]["child"].length>0){
              for(var j=0;j<wyList[i]["child"].length;j++){
                wyList[i]["child"][j]["point"] = false;
              }
            }
          }
          wyList[k]["child"][ki]["point"] = true;
      },
      loginout: function(){
        $.ajax({
          url: this.path + "loginOut",
          type: "get",
          dataType: "json",
          data: {
            "jobNumber": localStorage.getItem("jobNumber"),
            "sessionIdOa": localStorage.getItem("sessionIdOa") 
          },
          success: function(data){
            localStorage.clear();
            this.OACommon.delCookie("userId");
            this.$router.push('/');
          }.bind(this),
          error: function(err){
            console.log("退出登录异常");
          }
        });
      },
      getTreeData: function(){

        var leftPermissionList = localStorage.getItem("leftPermissionList");

        this.leftList = JSON.parse(localStorage.getItem("leftPermissionList"));
        var wl = this.leftList.length;
        this.wyLeftList = [];
        for(var i=0;i<wl;i++){
          this.wyLeftList.push({
            "sonShow": false,
            "menuj" : "menuj",
            "name": this.leftList[i]["permissionName"],
            "permissionIconUrl": this.leftList[i]["permissionIconUrl"],
            "child": []
          });
        }
        for(var j=0;j<wl;j++){
          if(this.leftList[j]["children"]&&this.leftList[j]["children"].length>0){
            for(var n=0;n<this.leftList[j]["children"].length;n++){
              this.wyLeftList[j]["child"].push({
                "point": false,
                "permissionIconUrl": this.leftList[j]["children"][n]["permissionIconUrl"],
                "childData": this.leftList[j]["children"][n] 
              });
            }
          }
        }
        

      },
     
              
    }
  }
  </script>
  <style>
    .point{background: #28384E;}
  </style>





