    countEduFee: function countEduFee(i) {
      if (!this.ed[i].startTime) {
        this.ed[i].errorTip = "请选择开始时间";
        return false;
      }
      if (!this.ed[i].endTime) {
        this.ed[i].errorTip = "请选择结束时间";
        return false;
      }
      var st = this.ed[i].startTime,
          et = this.ed[i].endTime;
      st = st.split("-").join(""), et = et.split("-").join("");
      st = Number(st), et = Number(et);
      if (st > et) {
        this.ed[i].errorTip = "开始时间不能大于结束时间";
        return false;
      }
      if (!this.ed[i].glf) {
        this.ed[i].errorTip = "请输入过路费";
        return false;
      }
      if (!this.ed[i].yf) {
        this.ed.errorTip = "请输入油费";
        return false;
      }
      this.ed[i].errorTip = "";

      this.ed[i].shCheck = true;
      //计算出差几天,出差补助计算
      var stTime = new Date(this.ed[i].startTime).getTime(),
          etTime = new Date(this.ed[i].endTime).getTime();
      var days = (etTime - stTime) / (1000 * 60 * 60 * 24);
      days += 1; //计算天数要加1
      //赋值
      var fuI = this.ed[i].popd;
      var fuK = this.ed[i].popdK;
      this.tableData[fuK]["sun"][fuI].c9.edval = Number(this.ed[i].glf) + Number(this.ed[i].yf) + days * this.ccbz;
      this.tableData[fuK]["sun"][fuI].c9.edObj = this.ed[i];

      this.countMoney();
    },
